import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import { useSelector } from 'react-redux';
import { Provider } from 'react-redux';
import { PersistGate } from 'redux-persist/integration/react'; // PersistGate 
import mediaQueries from './styles/MediaQuery';
import { ThemeConfig } from './styles/ThemeConfig';
import { GlobalStyle } from './styles/GlobalStyle';
import App from './App';
import { store, persistor, RootState } from './store'; // persistor 

const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);

const RootItem = () => {
  const bnwTheme = useSelector((state: RootState) => state.theme.bnwTheme); // Redux  

  const currentTheme = ThemeConfig(bnwTheme ? 'bnw' : 'color'); //    

  return (
    <BrowserRouter>
      <ThemeProvider theme={{ ...currentTheme, mediaQueries }}>
        <GlobalStyle />
        <App />
      </ThemeProvider>
    </BrowserRouter>
  );
};

root.render(
  <Provider store={store}>
    {/* PersistGate  */}
    <PersistGate loading={null} persistor={persistor}>
      <RootItem />
    </PersistGate>
  </Provider>
);
