import React, { useEffect, useRef } from 'react';
import * as S from './styles';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import useFetchData from '../../hooks/useFetchData';
import { ProjectsType } from '../../types/projects';
import SectionTitle from '../common/SectionTitle';
import TransparentText from '../common/Text/Transparent';
import { ThemeConfig } from '../../styles/ThemeConfig';
import { useSelector } from 'react-redux';
import { RootState } from '../../store';

gsap.registerPlugin(ScrollTrigger);

const Project = () => {
  const { data } = useFetchData<ProjectsType>('data/projects.json');
  const imageRefs = useRef<{ [key: string]: HTMLImageElement[] }>({});
  const bnwTheme = useSelector((state: RootState) => state.theme.bnwTheme);
  const currentMode = bnwTheme ? 'bnw' : 'color';
  const theme = ThemeConfig(currentMode);

  useEffect(() => {
    Object.values(imageRefs.current).forEach((projectImages) => {
      //        
      const tl = gsap.timeline({
        paused: true,
        defaults: { duration: 0.5 },
      });

      projectImages.forEach((image, index) => {
        if (image) {
          const transformTo =
            index === 0
              ? 'translate(-70%, -95%) rotate(-3deg)'
              : 'translate(-35%, -140%) rotate(2deg)';

          const defaultTransform = 'translate(-50%, -100%)';

          //   
          gsap.set(image, {
            transform: defaultTransform,
          });

          //    ScrollTrigger 
          ScrollTrigger.create({
            trigger: image,
            //      
            start: 'top 85%', //      
            end: 'bottom 15%', //      
            onToggle: (self) => {
              if (self.isActive) {
                //    
                gsap.to(image, {
                  transform: transformTo,
                  duration: 0.5,
                  overwrite: true,
                });
              } else {
                //    
                gsap.to(image, {
                  transform: defaultTransform,
                  duration: 0.5,
                  overwrite: true,
                });
              }
            },
            // toggleActions       
            toggleActions: 'play none play reverse',
          });
        }
      });
    });
  }, [data]);

  return (
    <S.Container>
      {data?.projects &&
        data.projects.map((project) => (
          <SectionTitle key={project.type} name={project.type}>
            {project.items &&
              project.items.map((item) => (
                <S.ItemWrapper
                  key={item.name}
                  hasMultipleItems={project.items.length > 1}
                >
                  <S.ProjectName>{item.name}</S.ProjectName>
                  {item.skills && item.skills.length > 0 && (
                    <S.SkillsWrap>
                      {item.skills.map((skill, index) => (
                        <TransparentText 
                          key={index}
                          customStyle={{
                            backgroundColor: theme.colors.textBox,
                            color: theme.colors.textDark,
                            border: `1px solid ${theme.colors.textBoxBorder}`
                          }}
                        >{skill}</TransparentText>
                      ))}
                    </S.SkillsWrap>
                  )}
                  <S.ProjectAbout>{item.about}</S.ProjectAbout>
                  {item.images && item.images.length > 0 && (
                    <S.ImagesWrap>
                      {item.images.map((image, index) => (
                        <S.Image
                          key={`${item.name}-${index}`}
                          ref={(el) => {
                            if (el) {
                              //     
                              if (!imageRefs.current[item.name]) {
                                imageRefs.current[item.name] = [];
                              }
                              imageRefs.current[item.name][index] = el;
                            }
                          }}
                          src={`/images/${image}.png`}
                          alt={`image-${index}`}
                        />
                      ))}
                    </S.ImagesWrap>
                  )}
                  <S.LinkWrap>
                    {item.visit && (
                      <TransparentText
                        customStyle={{
                          backgroundColor: theme.colors.dark,
                          color: theme.colors.background,
                          border: `1px solid ${theme.colors.dark}`,
                          borderRadius: '20px',
                        }}
                      >
                        <a
                          href={item.visit}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          Visit
                        </a>
                      </TransparentText>
                    )}
                    {item.github && (
                      <TransparentText
                        customStyle={{
                          backgroundColor: theme.colors.dark,
                          color: theme.colors.background,
                          border: `1px solid ${theme.colors.dark}`,
                          borderRadius: '18px',
                        }}
                      >
                        <a
                          href={item.github}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          GitHub
                        </a>
                      </TransparentText>
                    )}
                  </S.LinkWrap>
                </S.ItemWrapper>
              ))}
          </SectionTitle>
        ))}
    </S.Container>
  );
};

export default Project;
